import { ITimeLinesSolicitacaoView, ITimeLinesSolicitacaoController }
    from "../../contracts/Solicitacao/TimeLinesSolicitacaoContract";

import axios from "../../plugins/axios"

import { Storage } from "../../configuration/storage"
import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

export class TimeLinesSolicitacaoController implements ITimeLinesSolicitacaoController {

    _view: ITimeLinesSolicitacaoView

    constructor(view: ITimeLinesSolicitacaoView) {
        this._view = view
    }

    carregarItens(solicitacaoId: string): void {
        this._view.loading(true)

        axios.get('solicitacao/timeline/' + solicitacaoId).then((response: any) => {

            //
            this._view.loading(false)
            this._view.showItens(response.data)
        }).catch((e: any) => {
            console.log(e)
            this._view.loading(false)
            this._view.showMsg(e, 0)
        })
    }


}