














































import Vue from "vue";
import KLoading from "../../components/KLoading.vue";
import KDialog from "../../components/KDialog.vue";
import KDataList from "../../components/KDataList.vue";

import Component from "vue-class-component";
import { Watch } from "vue-property-decorator";

import { Display } from "../../configuration/display";
import { Storage } from "../../configuration/storage";

import { Header, HeaderIcon, HeaderAction } from "../../models/header";
import { Headers } from "../../models/headers";

import {
  ITimeLinesSolicitacaoView,
  ITimeLinesSolicitacaoController
} from "../../contracts/Solicitacao/TimeLinesSolicitacaoContract";

import { TimeLinesSolicitacaoController } from "../../controllers/Solicitacao/TimeLinesSolicitacaoController";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class TimeLinesSolicitacao extends Vue
  implements ITimeLinesSolicitacaoView {
  _controller?: ITimeLinesSolicitacaoController;

  isMobile = false;

  isSucesso = false;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  itens = [];

  idSolicitacao = "";

  solicitacao = {
    solicitante: "",
    filial: "",
    centroCusto: "",
    frete: "",
    dataVencimento: "",
    descricao: "",
    justificativa: "",
    user: ""
  };

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }

  fecharDialogMessage() {
    this.dialogMessage = false;
    if (this.isSucesso) {
      this.$router.push("/solicitacoes");
    }
  }

  showItens(list: []): void {
    this.itens = list;
  }

  showMsg(msg: string, modo: number): void {
    this.textError = msg;
    this.dialogMessage = true;
    if (modo == 1) {
      this.isSucesso = true;
    }
  }

  created() {
    this._controller = new TimeLinesSolicitacaoController(this);
    if (this.$route.params.id) {
      this.idSolicitacao = this.$route.params.id;

      this._controller.carregarItens(this.idSolicitacao);
    }
    this.isMobile = Display.isMobile();
  }
}
